export default {
  data: function data() {
    return {
      by: '支付宝',
      money: 1,
      remarks: '',
      notice: {
        show: false
      }
    };
  },
  model: {
    prop: 'value',
    event: 'valueChange'
  },
  props: {
    value: {
      default: false
    }
  },
  methods: {
    noticeHandle: function noticeHandle() {
      var _this = this;

      var formData = new FormData();

      if (!this.money) {
        this.$message.warning('金额输入有误');
        return;
      }

      formData.append('name', '图床');
      formData.append('by', this.by);
      formData.append('money', this.money);
      formData.append('remarks', this.remarks);
      fetch("".concat(this.$url.notice, "/donate"), {
        method: 'POST',
        body: formData
      }).then(function (res) {
        return res.json();
      }).then(function (res) {
        if (res.code === 200) {
          _this.$message.success('已经通知了喲');

          _this.notice.show = false;

          _this.$notification.success({
            message: '提示',
            description: '可以添加QQ群：791863400,查看捐助的钱去哪里了'
          });
        }
      }).catch(function () {
        _this.$message.success('通知服务器出现问题');
      });
    },
    showNotice: function showNotice() {
      this.$emit('valueChange', false);
      this.notice.show = true;
    },
    close: function close() {
      this.notice.show = false;
      this.$emit('valueChange', false);
    }
  }
};